        type: String,
        required: true,
        default: "",
    });

    courseProf = prop({
        type: String,
        required: true,
        default: "",
    });

    courseCredits = prop({
        type: String,
        required: true,
        default: "",
    });

    courseLectures = prop({
        type: String,
        required: true,
        default: "",
    });

    courseLabs = prop({
        type: String,
        required: true,
        default: "",
    });

    courseTerm = prop({
        type: String,
        required: true,
        default: "",
    });

    courseAvailability = prop({
        type: String,
        required: true,
        default: "",
