    courseCampus = prop({
        type: String,
        required: true,
        default: "",
    });

    courseMeetingsLectures = prop({
        type: String,
        required: true,
        default: "",
    });

    courseMeetingsLabs = prop({
        type: String,
        required: true,
        default: "",
    });

    courseCode = prop({
        type: String,
        required: true,
        default: "",
    });
