
import { prop, Vue } from "vue-class-component";

class YourCourseCardProps {
    yourCourseName = prop({
        type: String,
        required: true,
        default: "",
    });

    yourCourseProf = prop({
        type: String,
        required: true,
        default: "",
    });

    yourCourseCredits = prop({
        type: String,
        required: true,
        default: "",
    });

    yourCourseLectures = prop({
        type: String,
        required: true,
        default: "",
    });

    yourCourseLabs = prop({
        type: String,
        required: true,
        default: "",
    });
}

export default class YourCourseCard extends Vue.with(YourCourseCardProps) {}
